.wrapperPaginator {
    display: flex;
    justify-content: space-between;
    width: 600px;
    padding: 4px;
    margin: auto auto 30px;
}

.arrow {
    width: 20px;
}

.btn {
    background-color: transparent;
    border: none;
}

.active {
    font-weight: 600;
    font-size: 20px;
}

.span{
    cursor: pointer;
    padding: 5px;
}