.wrapper {
    position: fixed;
    top: 74px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
}

.block {
    text-align: center;
    color: aliceblue;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 15px;
    width: 280px;
    border-radius: 30px;
    padding: 20px;
    background: red;
}